<template>
  <div class="form">
    <form name="area">
      <div class="inputs">
        <input
          type="text"
          autocomplete="off"
          placeholder="Liczba a"
          name="aNumber"
        />
        <input
          type="text"
          autocomplete="off"
          placeholder="Liczba h"
          name="hNumber"
        />
        <input
          type="text"
          id="result"
          :value="result"
          autocomplete="off"
          placeholder="Wynik"
          disabled
        />
      </div>
      <button type="button" name="count" class="btn" @click="sumValues">
        Licz
      </button>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      result: "",
    };
  },
  methods: {
    sumValues() {
      var a, h, result;
      a = Number(document.area.aNumber.value);
      h = Number(document.area.hNumber.value);
      result = (a * h) / 2 + " j虏";
      this.result = result;
    },
  },
};
</script>
